import Vue from 'vue';
import VueRouter from 'vue-router';
import app from './App';
import vheader from 'component/mui/Header';

Vue.use(VueRouter);
var router = new VueRouter();

var HeaderVue = Vue.extend(vheader);

var header = new HeaderVue({
    data: function () {
        return {
            title: ''
        }
    },
    propsData: {
        title: '',
        hdstyle: {
            back: {
                display: true
            },
            title: {
                display: true
            }
        }
    },
    el: 'vheader'
});

//mui生命周期管理
mui.init({
    swipeBack: true
});

mui.plusReady(function () {
    router.start(app, '#app');
    header.title = plus.webview.currentWebview().guideName;
});
